.sidebarOptions {
    display: flex;
    flex-direction: column;
}

.sidebarOption {
    display: flex;
    align-items: center;
    padding: 10px;
    cursor: pointer;
}

.sidebarOption:hover {
    background-color: lightpink;
    border-radius: 5px;
}

.sidebarOption > img {
    height: 30px;
    margin-left: 20px;
    border-radius: 5px;
}

.sidebarOption > p {
    margin-left: 10px;
    color: rgb(97, 96, 96);
    font-weight: 400;
}

.sidebarOption > .MuiSvgIcon-root {
    font-size: medium;
    margin-left: 25px;
}
