.post {
    display: flex;
    flex-direction: column;
    padding: 10px;
    background-color: white;
    border: 1px solid lightgrey;
    border-radius: 5px;
    margin-top: 10px;
}

.post__info {
    display: flex;
    align-items: center;

}

.post__info > h4 {
    margin-left: 10px;
    cursor: pointer;
    font-size: 13 px;
}

.post__info > h4:hover {
    text-decoration: underline;
}

.post__info > small{
    margin-left: 10px;
}

.post__body {
    display: flex;
    flex-direction: column;
}

.post__body > .post__question {
    margin-top: 10px;
    font-weight: bold;
    margin-bottom: 10px;
    cursor: pointer;
    display: flex;
}

.post__body > .post__question > p:hover{
    text-decoration: underline;
}

.post__body > img {
    object-fit: contain;
    width: 100%;
    border-radius: 5px;
    cursor: pointer;
}

.post__question > .post__btnAnswer {
    margin-left: auto;
    outline: none;
    border: none;
    background-color: blueviolet;
    color: white;
    border: 10px;
    padding: 10px;
    border-radius: 5px;
}

.post__footer {
    display: flex;
    align-items: center;
    margin-top: 15px;
}

.post__footer > .MuiSvgIcon-root {
    color: gray;
    margin-right: 40px;
    cursor: pointer;
}

.post__footerAction {
    display: flex;
    justify-content: space-around;
    border-radius: 33px;
    padding: 5px;
    /* background-color: lightgray; */
    align-items: center;
    
}

.post__footerAction > .MuiSvgIcon-root {
    color: gray;
    margin-right: 40px;
    cursor: pointer;
}

.post__footerAction > .MuiSvgIcon-root:hover{
    color: blueviolet;
    margin-right: 40px;
    cursor: pointer;
}

.post__footerLeft {
    margin-left: auto;
}

.post__footerLeft > .MuiSvgIcon-root {
    color: gray;
    cursor: pointer;
    margin-left: 30px;
}