.qHeader {
    display: flex;
    /* justify-content: center; */
    align-items: center;
    padding: 1px;
    background-color: white;
    width: 100%;
    position: sticky;
    z-index: 1000;
    top: 0;
    box-shadow: 0px 5px 8px -9px rgba(0, 0, 0,0.5);
}

.qHeader__logo > img {
    height: 80px;
    width: 80px;
    margin-left: 160px;
}

.qHeader__icons {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 550px;
}

.qHeader_icon > .MuiSvgIcon-root {
    color:blueviolet;
    cursor: pointer;
    font-size: xx-large;
    padding: 5px 20px;
}

.qHeader_icon > .MuiSvgIcon-root:hover {
    color: white;
    border-radius: 10px;
    background-color: blueviolet;
}

/*
    SearchBar
*/
.qHeader__searchicon{
    position: absolute;
    border-radius: 50%;
    margin-left: 10px;
    transition: filter 300ms;
  }
  
  .qHeader__input_container {
    margin-left: auto;
    display: flex;
    flex-basis: 3px;
    flex-grow: 0.5;
    align-items: center;
  }
  
  .qHeader__input{
    margin: 0 auto;
    display: block;
    width: 100%;
    height: 45px;
    padding: 0 30px 0;
    font-size: 1rem;
    outline: none;
  }
  

