.qHeader__Rem {
    display: flex;
    align-items: center;
    margin-left: 25px;
    margin-right: 20px;
}

.qHeader__Rem > .qHeader__avatar {
    cursor: pointer;
}

.qHeader__Rem > .MuiSvgIcon-root {
    cursor: pointer;
    font-size: xx-large;
    color: gray;
    margin-left: 25px;
}

.qHeader__Rem > .MuiSvgIcon-root:hover {
    color: blueviolet;
}

.qHeader__Rem > .MuiButton-root {
    margin-left: 25px;
    border-radius: 20px;
    cursor: pointer;
}