.gmBox {
    display: flex;
    flex-direction: column;
    padding: 10px;
    border: 1px solid lightgray;
    background-color: white;
    border-radius: 5px;
    cursor: pointer;
}

.gmBox__avatar{
    margin-right: 10px;
}
.gmBox:hover {
    border: 1px solid grey;
}
.gmBox__info {
    display: flex;
    align-items: center;
}

.gmBox__info > h5 {
    color: rgb(0, 0, 0);
    font-weight: 300;
    margin-left: 10px;
}

.gmBox__gm {
    display: flex;
    margin-top: 8px;
}

.gmBox__gm > input {
    color: rgb(70, 70, 70);
    width: 100%;
    height: 100%;
    font-weight: bold;
    font-size: large;
}

.gmBox__btnAsk{
    margin-right: auto;
    outline: none;
    border: none;
    background-color: blueviolet;
    margin-top: 10px;
    color: white;
    border: 10px;
    padding: 10px;
    border-radius: 5px;
}